body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(66, 66, 66);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and(max-width:620px) {
  .home-content h1{
    font-size: 20px;
    position: absolute;
    color: white;
  }
  .about-element h3 {
    font-size: 10px;
  }
  
}
@media screen and (min-width:900px) {
  .career-text p {
    font-size: 28px;
  }
  .about-element h3 {
    font-size: 40px;
  }
  
}
.introduce h3 {
  border-bottom: 2px solid rgb(247, 124, 236);
  font-size: 40px;
}
.about-element h3 {
  border-bottom: 2px solid rgb(124, 247, 134);
  
}
.introduce, .about-element {
  width: 80%;
  
  margin: 60px 100px;
}
.introduce p, .about-element p{
  font-size: 20px;
}
.about-element ul {
  padding-left: 10px;
  padding-top: 4px;
  padding-bottom: 20px;
  margin-bottom: 100px;
  background-color: #505050;
  border-radius: 10px;
}
.about-element li {
  list-style: none;
  margin-top: 20px;
}
.introduce h5, .about-element h5 {
  font-size: 20px;
  margin-bottom: 8px;
  margin-top: 4px;
}
.about-element a {
  font-size: 24px;
  color: aliceblue;
  background-color: #505050;
  padding: 10px;
  margin-top: 6px;
  margin-left: 12px;
  margin-right: 12px;
  border-radius: 8px;
  /* text-decoration: none; */
  display: inline-block;
  transition: all 0.3s;
  
} 
.about-element a:hover {
  font-size: 24px;
  color: rgb(65, 131, 70);
  background-color: #fff;
}

.title h1 {
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: white;
}

.home-content {
  position: absolute;
  color: white;
}
.home-content h1 {
  font-size: 3em;
  margin: 100px auto;
  font-family: Georgia, 'Times New Roman', Times, serif;
}



